	module.exports = {"data":{"markdownRemark":{"html":"<h2>“<a href=\"/filez/music/bnh.mp3\">BRAND NEW HEART” を聴く</a> ([filesize]<a href=\"https://danmaq.com/filez/music/bnh.mp3%5B/filesize%5D\">https://danmaq.com/filez/music/bnh.mp3[/filesize]</a>)**</h2>\n<p> 猫蟹丸さん作 “Brand New Heart (Tr-2nd Mix)” のハイスピード Mix です。 初のBMS製作、いろんな意味で習作です。当時はオリジナル楽曲を考える程度の能力を持ち合わせていなかったので、当時BM98の練習用として猿の様にプレイしていた猫蟹丸様の“BRAND NEW HEART”をリミックスさせて戴きました。こちらも“ToHeart”からのリミックスなので私のはリミックスのリミックスか。</p>\n<h2>2009/9/7追記</h2>\n<p>難易度設定を7段階から12段階へ引きなおしました。旧難易度は7です。(譜面自体は弄っていません)</p>\n<h2>注意</h2>\n<p>本作品は原曲の<em>差分</em>となっております。このBMS単体ではプレイできません。なお、原曲のいかなるファイルも上書きしないので、安心してください。 **原作の著作権情報</p>\n<ul>\n<li>\n<p>猫蟹丸 / BRAND NEW HEART (Tr-2nd Mix)</p>\n</li>\n<li>\n<p><a href=\"http://knm.sakura.ne.jp/\">猫蟹丸様のWebサイトへ行く</a></p>\n</li>\n<li>\n<p>euro house | 1:35 | 167BpM | BMSLEVEL:9 | BMSNOTES:839</p>\n</li>\n<li>\n<p><a href=\"/filez/music/bnh_2ndH.zip\">BMS “BRAND NEW HEART” をダウンロード</a> ([filesize]<a href=\"https://danmaq.com/filez/music/bnh_2ndH.zip%5B/filesize%5D\">https://danmaq.com/filez/music/bnh_2ndH.zip[/filesize]</a>)</p>\n</li>\n</ul>","frontmatter":{"cover":null,"date":"2000-05-05T00:24:40+09:00","strDate":"2000/5/4","title":"[BMS]BRAND NEW HEART"}}},"pathContext":{"path":"/ja/blog/2000/05/05-bms-brand-new-heart/","langKey":"ja"}}

